html {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  overflow: hidden;
}

.page {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.header {
  flex-shrink: 0;
  width: 100%;
  height: 56px;
  background: #243445;
}

.tab-bar {
  flex-shrink: 0;
  width: 80px;
  height: 100%;
  background: #243445;
}

.content {
  flex-grow: 1;
  flex-shrink: 1;
}
